 const playMode = {
  sequence: 0,
  loop: 1,
  random: 2
}


const state = {
  singer: {},
  rankList: {},
  playing: false,
  fullScreen: false,
  playlist: [],
  sequenceList: [],
  mode: playMode.sequence,
  currentIndex: -1,
}

export default state




